<template>
  <div class="cartcontrol">
    <transition name="move">
      <div class="subtract icon-remove_circle_outline" @click="subtract" v-show="food.count>0"></div>
    </transition>
    <div class="count" v-show="food.count>0">{{ food.count }}</div>
    <div class="add icon-add_circle" @click="add"></div>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: "name",
  props:{
    food:{
      type: Object
    }
  },
  data: function() {
    return {};
  },
  methods: {
    add(event){
      if(!event._constructed){
        return
      }
      if(!this.food.count){
        Vue.set(this.food,'count',1)
      }else{
        this.food.count++
      }
    },
    subtract(event){
      if(!event._constructed){
        return
      }
      if(this.food.count){
        this.food.count--
      }
    }
  }
};
</script>

<style lang='scss' scoped>
.cartcontrol{
  .subtract{
    display: inline-block;
    padding:6px;
    font-size: 24px;
    line-height: 24px;
    color: rgb(0,160,220);
    &.move-enter-active,&.move-leave-active{
      transition: all 0.4s linear
    }
    &.move-enter,&.move-leave-to{
      opacity: 0;
      transform: translateX(24px) rotate(180deg)
    }
  }
  .count{
    display: inline-block;
    vertical-align: top;
    text-align: center;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    font-size: 10px;
  }
  .add{
    display: inline-block;
    font-size: 24px;
    line-height: 24px;
    color: rgb(0,160,220);
    padding:6px;
  }
}
</style>