<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li v-for="(item,index) in goods" :class="{'current':currentIndex == index}" 
        @click="selectMenu(index,$event)">
          <div class="text-wrapper"  >
            <span v-show="item.foods > 0" class="icon-arrow_lift"></span>
            <span class="text border-1px">{{ item.name }}</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="item in goods" class="foodListHook">
          <h1 class="title">{{ item.name }}</h1>
          <ul>
            <li class="food-item border-1px" v-for="food in item.foods">
              <div class="icon">
                <img width="57" height="57" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{ food.name }}</h2>
                <p class="desc">{{ food.description }}</p>
                <div class="extra">
                  <span class="count">月售{{ food.sellCount }}份</span>
                  <span>好评率{{ food.rating }}%</span>
                </div>
                <div class="price">
                  <span class="now">
                    <span class="nowMin">￥</span>
                    {{ food.price }}
                  </span>
                  <span class="old" v-show="food.oldPrice">￥{{ food.oldPrice }}</span>
                </div>
                <dir class="cartcontrol-wrapper">
                    <cartcontrol :food="food"/>
                </dir>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopCar :selectFoods='selectFoods' :deliveryPrice='seller.deliveryPrice' :minPrice='seller.minPrice' />
  </div>
</template>

<script>
import BScroll from "@better-scroll/core";
import shopCar from '../shop-car/shop-car'
import cartcontrol from '../cartcontrol/cartcontrol'

export default {
  name: "name",
  props: ["seller"],
  components:{
      shopCar,
      cartcontrol
  },
  data: function() {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0
    };
  },
  // created() {
  //   var error = 0;
  //   this.$http.get("/api/goods").then(res => {
  //     let newRes = res.data;
  //     if (newRes.errno === 0) {
  //       this.goods = newRes.data;
  //       this.$nextTick(() => {
  //         this._initScroll();
  //         this._calculateHeight();
  //       });
  //     }
  //   });
  // },
  created() {
    var error = 0;
    this.$http.get("http://www.lianxianzhong.top/mock/eleme.json").then(res => {
      let newRes = res.data;
      console.log(newRes);
      
      if (newRes) {
        this.goods = newRes.goods;
        this.$nextTick(() => {
          this._initScroll();
          this._calculateHeight();
        });
      }
    });
  },
  computed: {
    currentIndex() {
      for (var i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i;
        }
      }
      return i
    },
    selectFoods(){
        let foodArray = []
        this.goods.forEach((good)=>{
            good.foods.forEach((food)=>{
                if(food.count>0){
                    foodArray.push(food)
                }
            })
        })
        return foodArray
    }
  },
  methods: {
    selectMenu(index,event){
        if(!event._constructed){
            return
        }
        let foodList = this.$refs["foodsWrapper"].getElementsByClassName("foodListHook")
        let el = foodList[index]
        this.foodsScroll.scrollToElement(el,300)
        
    },
    _initScroll() {
      this.menuScroll = new BScroll(this.$refs["menuWrapper"], {
          click:true
      });
      this.foodsScroll = new BScroll(this.$refs["foodsWrapper"], {
        click:true,
        probeType: 3
      });
      this.foodsScroll.on("scroll", pos => {
        this.scrollY = Math.abs(Math.round(pos.y));
      });
      
      
    },
    _calculateHeight() {
      let foodList = this.$refs["foodsWrapper"].getElementsByClassName(
        "foodListHook"
      );
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i++) {
        height += foodList[i].clientHeight;
        this.listHeight.push(height);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../common/scss/mixin";
.goods {
  position: absolute;
  top: 174px;
  bottom: 46px;
  display: flex;
  width: 100%;
  overflow: hidden;
  .menu-wrapper {
    flex: 0 0 80px;
    width: 80px;
    background-color: #f3f5f7;
    .current {
        background-color: #fff;
        z-index: 10 ;
    }
    .text-wrapper {
      width: 56px;
      height: 54px;
      line-height: 14px;
      padding: 0 12px;
      display: flex;
      align-items: center;
      justify-content: center;
      display: table;
      .text {
        width: 56px;
        display: table-cell;
        vertical-align: middle;
        font-size: 12px;
        font-weight: 100;
        @include border-1px(rgba(7, 17, 27, 0.1));
      }
    }
  }
  .foods-wrapper {
    flex: 1;
    background-color: #fff;
    .title {
      height: 26px;
      border-left: 2px solid #d9dde1;
      padding-left: 14px;
      font-size: 12px;
      line-height: 26px;
      color: rgb(147, 153, 159);
      background-color: #f3f5f7;
    }
    .food-item {
      margin: 18px;
      display: flex;
      flex-direction: row;
      padding-bottom: 18px;
      @include border-1px(rgba(7, 17, 27, 0.1));
      &:last-child {
        @include border-none;
      }
      .icon {
        flex: 0 0 57px;
        margin-right: 10px;
      }
      .content {
        flex: 1;
        .name {
          font-size: 14px;
          color: rgb(7, 17, 27);
          line-height: 14px;
          margin-top: 2px;
          margin-bottom: 8px;
        }
        .desc,
        .extra {
          font-size: 10px;
          color: rgb(147, 153, 159);
          line-height: 10px;
        }
        .desc {
          margin-bottom: 8px;
          line-height: 12px;
        }
        .extra {
          .count {
            margin-right: 12px;
          }
        }
        .price {
          font-size: 14px;
          font-weight: 700px;
          line-height: 24px;
          color: rgb(240, 20, 20);
          .now {
            margin-right: 8px;
            .nowMin {
              font-size: 10px;
            }
          }
          .old {
            font-size: 10px;
            color: rgb(147, 153, 159);
            text-decoration: line-through;
          }
        }
        .cartcontrol-wrapper{
            position: absolute;
            bottom: 0px;
            right: 0px;
        }
      }
    }
  }
}
</style>