<template>
  <div class="shop-car">
    <div class="content">
      <div class="content-left">
        <div class="logo-wrapper">
          <div class="logo" :class="{'highlight':totalCount>0}"">
            <span class="icon-shopping_cart"></span>
          </div>
          <div class="num" v-show="totalCount>0"><span>{{totalCount}}</span></div>
        </div>
        <div class="price" :class="{'highlight':totalCount>0}">
          <span>￥{{ totalPrice }}</span>
        </div>
        <div class="desc">
          <span>另需配送费￥{{ deliveryPrice }}元</span>
        </div>
      </div>
      <div class="content-right">
        <div class="pay" :class="payClass">
          {{ payDesc }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "name",
  props: {
    deliveryPrice: {
      type: Number,
      default: 0
    },
    minPrice: {
      type: Number,
      default: 0
    },
    selectFoods: {
      type: Array,
      default() {
        return [
          {
            price:20,
            count:1
          }
        ];
      }
    }
  },
  data: function() {
    return {};
  },
  methods: {},
  computed:{
    totalPrice(){
      let total = 0;
      this.selectFoods.forEach((food)=>{
        total += food.price * food.count
      })
      return total
    },
    totalCount(){
      let count = 0;
      this.selectFoods.forEach((food)=>{
        count += food.count
      })
      return count
    },
    payDesc(){
      if(this.totalPrice === 0){
        return `￥${this.minPrice}起送`
      }else if(this.totalPrice < this.minPrice){
        return `还差￥${this.minPrice - this.totalPrice}起送`
      }else{
        return '去结算'
      }
    },
    payClass(){
      if(this.totalPrice >= this.minPrice){
        return 'payHighlight'
      }else{
        return 'pay'
      }
    }
  }
};
</script>

<style lang='scss' scoped>
.shop-car {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 48px;
  width: 100%;
  background: #141d27;
  .content {
    display: flex;
    .content-left {
      flex: 1;
      display: flex;
      flex-direction: row;
      .logo-wrapper {
        width: 48px;
        height: 48px;
        margin: 0 18px;
        padding: 5px;
        background: #141d27;
        position: relative;
        bottom: 8px;
        border-radius: 50%;
        box-sizing: border-box;
        .num{
          width: 24px;
          line-height: 16px;
          position: absolute;
          right: 0;
          top: 0;
          font-size: 9px;
          background-color: rgb(240,20,20);
          color: #fff;
          box-shadow: 0px 4px 8px 0px rgb(0,0,0,.4);
          text-align: center;
          border-radius: 24px;
        }
        .logo {
          width: 100%;
          height: 100%;
          background: rgb(43, 52, 60);
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 24px;
          color: rgb(255, 255, 255, 0.4);
          &.highlight{
            background-color: rgb(0,160,220);
            color: #fff;
          }
        }
      }
      .price {
        height: 24px;
        font-size: 16px;
        color: rgb(255, 255, 255, 0.4);
        line-height: 24px;
        margin-top: 12px;
        font-weight: 700;
        padding-right: 12px;
        border-right: 1px solid rgb(255, 255, 255, 0.1);
        box-sizing: border-box;
        &.highlight{
            color:#fff;
          }
      }
      .desc {
        font-size: 10px;
        color: rgb(255, 255, 255, 0.4);
        line-height: 24px;
        margin: 12px 0 0 12px;
      }
    }
    .content-right {
      flex: 0 0 105px;
      width: 105px;
      .pay {
        height: 48px;
        font-size: 12px;
        color: rgb(255, 255, 255, 0.4);
        line-height: 48px;
        text-align: center;
        font-weight: 700;
        background-color: #2b333b;
        &.payHighlight{
          background-color: #38ca73;
          color:#fff;
        }
      }
    }
  }
}
</style>